body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  background-color: black;
  color: white
}

#root{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* ----------- DESKTOP -----------*/
@media (min-width: 768px){
  .page{
    padding: 0 15px;
    flex-grow: 1;
    max-width: 1600px;
  }
  
  .about{
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    gap: 30px;
  }

  .home-text{
    width: 50%;
    font-size: 20px;
  }

  .home-pic{
    width: 60%;
  }
  
  .big-heading{
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  .heading{
    font-weight: bold;
    font-size: 20px;
  }
  
  .pic{
    max-height: 350px;
  }
  
  .divider{
    background-color: white;
    height: 2px;
    width: 100%;
    margin: 30px 0;
  }
  
  .cards{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  .card{
    margin-bottom: 15px;
    max-width: 350px;
  }

  .side-card{
    margin-bottom: 15px;
    display: flex;
    gap: 30px;
  }
  
  .credit{
    font-size: 12px;
    color: #888888;
  }
  
  .credit-link{
    color: #888888;
  }
  
  .card-heading{
    font-weight: bold;
    font-size: 25px;
  }
  
  .card-body{
    display: flex;
    flex-direction: column;
  }

  .presents-body{
    align-items: center;
    margin-bottom: 30px;
  }

  .presents{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .gray{
    color: #888888;
  }
  
  .photo{
    vertical-align: bottom;
  }
  
  .socials{
    display: flex;
    justify-content: center;
    gap: 5px;
    margin: 15px 0;
  }
  
  .socials-footer{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
  }
  
  .icon-big{
    max-height: 50px;
  }
  
  .icon{
    height: 20px;
    width: 20px;
    vertical-align: bottom;
  }
  
  .footer{
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    font-size: 12px;
    padding: 10px;
    justify-content: space-between;
    border-top: 1px solid white;
    width: 100%;
    box-sizing: border-box;
  }
  
  .ext-link{
    display: flex;
    align-items: center;
    gap: 3px
  }
  
  .to-eventbrite {
    background-color: #ec1f24; /* Green */
    border: none;
    width: 100%;
    margin-bottom: 15px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
  }
  
  .venues{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
}

